/* App.css */

body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  width: 100%;
  max-width: 1080px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section {
  width: 100%;
}

.section1, .section3 {
  width: 100%;
  background-color: #125520;
}

.section1 {
  height: 149px;
}

.section2 {
  flex: 1;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.section2 .background-image {
  width: 100%;
  height: auto;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.section2 .text {
  width: 80%;
  max-width: 747px;
  color: white;
  text-align: center;
  position: relative;
  z-index: 1;
}

.text .small-text {
  font-family: NanumSquare;
  font-size: 4vw;
  font-weight: 800;
  line-height: 1.2;
}

.text .large-text {
  font-family: NanumSquare;
  font-size: 8vw;
  font-weight: 800;
  line-height: 1.2;
}

.section3 {
  height: 149px;
}

.order-button {
  display: inline-block;
  margin-top: 50px;
  padding: 20px 40px;
  background-color: #ffc107;
  color: #000;
  text-decoration: none;
  font-size: 24px;
  border-radius: 5px;
  position: relative;
  z-index: 1;
}

.order-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
}

.order-option {
  margin: 20px;
  padding: 20px 40px;
  font-size: 24px;
  cursor: pointer;
}

.back-button {
  display: inline-block;
  margin-top: 30px;
  padding: 10px 20px;
  background-color: #125520;
  color: #fff;
  text-decoration: none;
  font-size: 18px;
  border-radius: 5px;
}

/* Media Queries for Responsive Design */
@media (max-width: 1200px) {
  .text .small-text {
    font-size: 3.5vw;
  }

  .text .large-text {
    font-size: 7vw;
  }

  .order-button {
    font-size: 20px;
    padding: 15px 30px;
  }
}

@media (max-width: 992px) {
  .text .small-text {
    font-size: 3.5vw;
  }

  .text .large-text {
    font-size: 7vw;
  }

  .order-button {
    font-size: 18px;
    padding: 12px 25px;
  }
}

@media (max-width: 768px) {
  .text .small-text {
    font-size: 4vw;
  }

  .text .large-text {
    font-size: 8vw;
  }

  .order-button {
    font-size: 16px;
    padding: 10px 20px;
  }
}

@media (max-width: 576px) {
  .text .small-text {
    font-size: 5vw;
  }

  .text .large-text {
    font-size: 10vw;
  }

  .order-button {
    font-size: 14px;
    padding: 8px 15px;
  }
}
